const responseMenu = $ => {
	const iconClass = 'header__mobile-icon';
	const menuClass = 'header__menu-container';
	const openedMod = '_opened';

	const icon = $('.' + iconClass);
	const menu = $('.' + menuClass);

	icon.on('click', function () {
		menu.toggleClass(menuClass + openedMod);
		icon.toggleClass(iconClass + openedMod);
	});
};

module.exports = responseMenu;



